@use "@db-ui/base/build/scss/variables" as *;
@use "@db-ui/base/build/scss/helpers/functions" as *;
@use "@db-ui/base/build/scss/icon/icons.helpers" as *;
@import "../../../css/partials.meta";
@import "link-list.variables";

.cmp-link-list {
	list-style: none;

	padding-left: 0;

	li {
		.elm-link {
			@include icon(glyph(chevron-right), $partial: $partial);

			font-weight: 700;
			text-decoration: none;

			&:hover,
			&:active {
				text-decoration: underline;
			}

			&::before {
				color: $db-colors-primary-enabled;
				display: inline-block;
				font-size: to-rem($pxValue: 12);
				font-weight: 700;
				margin-top: -#{to-rem($pxValue: 2)};
				vertical-align: middle;
			}
		}
	}
}
